  h3 {
    position: absolute;
  }

  img {
    cursor: pointer;
  }

  .drinksFilter {
    margin: auto;
    width: fit-content;

    span {
      padding: 0 10px;
    }

    input, label {
      cursor: pointer;
    }

    label {
      margin-left: 5px;
    }
  }
}